
























































/* 仅添加需要重置reset的签 ---start---*/
body,div,p,ul,li,a,dl,dt,dd,img,ol,input,table,tr,td,textarea,h1,h2,h3,h4,h5,h6,select {
  margin: 0;
  padding: 0;
  outline: none;
}
address, caption, cite, code, dfn, em, th, var, i, samp {
  font-style:normal;
  font-weight:normal;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display:block;
  *zoom:1;
}
q:before, q:after {
  content:'';
}
body,button,input,select,textarea {
  font:12px/1.5 SimSun, Helvetica, Tahoma, Arial, \5FAE\8F6F\96C5\9ED1, sans-serif;
  *line-height: 1.5;
  -ms-overflow-style: scrollbar;  
  color:#333;
  margin:0 auto;
  background:#fff;
}
/*禁止FF等浏览器给textarea增加可拖拽大小的设置*/
textarea {
  resize: none;
}
/*图片无边框*/
img {
  border: none;
  vertical-align: top;
}
input {
  border: 0;
  outline: none;
}
/*IE input X 去掉文本框的叉叉和密码输入框的眼睛图标*/
::-ms-clear, ::-ms-reveal{display: none;
}
sup {
  vertical-align: text-top
}
sub {
  vertical-align: text-bottom
}
/* 清除列表样式设置，以下为示例，仅写你用到的标签 */
ul,li {
  list-style: none;
}
/*表格边框合并*/
table {
  border-collapse: collapse;
  border-spacing: 0
}
table,td,th,tr {
  border-collapse: collapse;
}
/*修复火狐按钮文字不垂直居中的bug*/
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
  padding: 0;
}

/* 仅添加需要重置reset的签 ---end---*/
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #434343;
  font-family:"Microsoft Yahei","Hiragino Sans GB","Helvetica Neue",Helvetica,tahoma,arial,"WenQuanYi Micro Hei",Verdana,sans-serif,"\5B8B\4F53";
  margin-top: 60px;
}
.title{
  text-align: center;
  margin-bottom:10px;
}
.inputBox{
    margin: 0 auto;
    width: 630px;
    height: 40px;
    border-radius: 10px;
    border: 1px solid #CFCFCF;
    font-size: 18px;
}
.focus{
      background-color: #fafffe;
      border-color: #3bb4f2;
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(59,180,242,.3);
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(59,180,242,.3);
}
.focus input{background-color:#fafffe
}
.inputBox input{
    width: 610px;
    line-height: 40px;
    margin-left: 10px;
    font-size: 14px;
    color: #000;
    font-weight: 500;
}
.list{
  width: 610px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
}
.list a{
  text-decoration: none;
  color: #000;
}
.finished label{
  padding-right: 20px;
  position: relative;
}
.list input{
  margin-right: 5px;
}
.finished{
  text-decoration: line-through;
}
.finished label:after{
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  position: absolute;
  background: url(assets/resizeApi.png);
  right: 0;
  top: 4px;
  cursor: pointer;
}
.finished label:after:hover{
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  -webkit-transition: all 1s;
  transition: all 1s;
}



